@import url(https://use.fontawesome.com/releases/v5.7.0/css/all.css);
@import url(https://fonts.googleapis.com/css?family=Quicksand:200,300,400,700);
body {
  font-family: Quicksand !important; }

#map {
  height: 300px;
  border-radius: 20px 20px 0px 0px;
  -moz-border-radius: 20px 20px 0px 0px;
  -webkit-border-radius: 20px 20px 0px 0px;
  border: 0px solid #000000;
  box-shadow: 0px 10px 20px -10px rgba(0, 0, 0, 0.4); }
  #map .leaflet-popup-content p {
    margin: 6px; }

#filters-bar a {
  font-size: 20px !important;
  color: #6f7f8c; }
  #filters-bar a .active, #filters-bar a:active, #filters-bar a:hover {
    text-decoration: underline !important;
    color: #1e2b37; }

#board .card:hover {
  box-shadow: 0px 0px 2px 0px #6f7f8c; }

#board .job-title a {
  font-size: 20px !important;
  color: #1e2b37; }
  #board .job-title a .active, #board .job-title a:active, #board .job-title a:hover {
    text-decoration: underline !important;
    color: #1e2b37; }

#board .job-card-img {
  width: 60%; }

#board .salary {
  color: #1e2b37; }

#job .job-card {
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-radius: 0.25rem;
  padding: 20px 20px 20px 20px;
  margin-top: 15px; }
  #job .job-card .job-lvl .muted {
    color: #6f7f8c; }
  #job .job-card .job-lvl .exhibited {
    color: #1e2b37;
    text-decoration: underline; }
  #job .job-card .job-skills .fas {
    color: #1e2b37; }
  #job .job-card .job-skills .far {
    color: #6f7f8c; }
  #job .job-card:hover {
    box-shadow: 0px 0px 2px 0px #6f7f8c; }

#job a {
  font-size: 20px !important;
  color: #1e2b37; }
  #job a .active, #job a:active, #job a:hover {
    text-decoration: underline !important;
    color: #1e2b37; }

#job img {
  width: 80%; }

